.max-width{
        max-width: 1100px;
        margin: 0px auto;
}
.abolute-center{
    display: flex;
    justify-content: center;
    align-items: center ;
}
.cur-po{
    cursor: pointer;
}
.collection-title{
    font-size: 30px;
    color: var(--heading-text);
    margin-bottom: 32px;
    font-weight: 600;

}
.slick-prev:before{
    color: var(--gray);
}
.slick-next:before{
    color: var(--gray);
}