.tab-option{
    box-shadow: var(--border-white) 0px -0.5px 0px inset;
}
.option-wrapeer{
    display: flex;
}
.tab-item {
    margin-right: 24px;
    padding: 12px 12px 20px 12px;
}
.tab-name{
    font-size: 20px;
    font-weight: 600;
    margin-left: 10px;
    color: var(--inactive-text);
}
.tab-img-container{
    height: 60px;
    width: 60%;
    border-radius: 50%;
    background-color: var( --inactive-color);
}
.tab-img{
    height: 30px;
    width: 30px;
}
.active-tab{
    border-bottom: 2px solid var(--zomato-red);
}
.active-tab .tab-name{
    color: var(--zomato-red);
}