.header-logo{
    height: 27px;
    max-width: 140px;
    margin-right: 24px;
    color: rgb(199, 179, 179);
}
.header{
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 12px;
}
.header-right{
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    height: 72px;
}
.header-location-search-container{
    height: 54px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    box-shadow: var(--primary-box-shadow);
    border: 1px solid var( --border-white);
    width: 70%;
    background-color: var(--primary-bg);
}
.location-wrapper{
display: flex;
flex: 1;
justify-content: space-between;
padding: 0px 10px;
}
.location-icon-name{
    display: flex;
    color: var(--zomato-gray);
}
.location-icon{
    color: var(--zomato-red);
    font-size: 20px;
    margin-right: 8px;
}
.location-search-seperator{
    height:20px;
    margin-left: 10px;
    border: .5px solid var(--gray);
}
.header-search-bar{
   display: flex;
    flex: 2;
}
.search-icon{
    font-size: 20px;
    color: var(--zomato-gray);
    margin: 0px 12px;
}
.search-input{
    border: none;
    width: 100%;
     outline: none;
     font-size: 14px;
}
.profile-wrapper{
    display: flex;
    justify-content: space-between;
    width: 15%;
    align-items: center;
}
.header-profile-image{
    height: 40px;
    width: 40%;
    border-radius: 50%;
}
.header-user-name{
    font-size: 16px;
    font-weight: 600;
}
.profile-option-icon{
    font-size: 20px;
}